<template>
  <v-container fluid pa-0>
    <v-row no-gutters>
      <v-col cols="12" v-for="(station, index) in stationList" :key="index">
        <Panel 
          :station="station.value"
        ></Panel>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { KunlunApi, hostname } from '../api'
import store from '../store'
import Panel from './Panel'

export default {
  name: 'StationPage',
  components: {
    Panel
  },
  data: () => ({
    url: '',
    ws: null,
    timeOutCount: null,
    stationList: []
  }),
  mounted () {
    this.url = window.location.hash.substring(1)  // "/kunlun"
    this.initSocket()
  },
  beforeDestroy () {
    this.ws.close()
  },
  methods: {
    initSocket () {
      // ws = "ws://localhost:8000/ws/kunlun/pcbst"
      this.getStationList()
      const url = hostname.replace('http', 'ws') + '/ws' + this.url
      this.ws = new WebSocket(url)
      // this.ws.onmessage = this.onMessageSocket
      this.ws.onerror = this.onErrorSocket
      this.ws.onclose = this.onCloseSocket
    },
    onErrorSocket () {
      store.commit('setMessage', "WS Connection Lost")
      setTimeout(() => {
        this.initSocket()
      }, 3000)
    },
    onSendSocket (data) {
      this.ws.send(JSON.stringify(data))
    },
    onCloseSocket () {
      return false
    },
    // onMessageSocket (e) {
    //   const data = JSON.parse(e.data)
    //   console.log(data)
    // },
    getStationList () {
      KunlunApi({ service: 'get-all-stations' })
        .then(response => {
          this.stationList = response.data.data
          // console.log(response.data)
          // this.message = response.data.data
          store.commit('setMessage', response.data.message)
        })
        .catch(e => {
          console.log(e)
        })
    },
  }
}
</script>
